.container_shadow{
  box-shadow: 0px 2px 92px 0px rgba(0,0, 0, 0.13);
  -webkit-box-shadow:  0px 2px 92px 0px rgba(0,0, 0, 0.13);
  -moz-box-shadow:  0px 2px 92px 0px rgba(0,0, 0, 0.13);

}


.main-content{
  background-color: white;
  min-height: 100vh;
  border-radius: 6px;
  -moz-border-radius: 6px;
  -webkit-border-radius: 6px;
  margin-bottom: 30px;
  padding-top: 10px;
  


}

.profile{
  height:800px;
  border-radius: 6px;
}


.section_title{
  position: relative;
  display: inline-block;
  padding:  0 3px;
}
.section_title .section_title_txt{
  font-size: 18px;
  font-weight: 600;
  position: relative;
  margin:0;
}

.section_title span{
  position: absolute;
  height: 7px;
  width: 11%;
  bottom: 0;
  opacity: 0.4;
  left:0;
  background-color: var(--main-color);
}
